  function showHarmonisationModal(rows, excelRange, theme) {
    const modal = document.getElementById('modal-harmonisation');
    const tbody = document.querySelector('#harmonisation-table tbody');
    tbody.innerHTML = '';
    
    // Crer le datalist pour l'autocompltion
    let datalist = document.getElementById('harmonisation-values');
    if (!datalist) {
      datalist = document.createElement('datalist');
      datalist.id = 'harmonisation-values';
      document.body.appendChild(datalist);
    }
    
    // Rcuprer toutes les valeurs harmonises uniques
    const uniqueHarmonises = new Set();
    if (window.bddWorkbook && theme) {
      const sheet = window.bddWorkbook.Sheets[theme];
      const data = XLSX.utils.sheet_to_json(sheet, { header: 1 });
      for (let i = 1; i < data.length; i++) {
        if (data[i] && data[i][1]) uniqueHarmonises.add(data[i][1]);
      }
    }
    
    // Mettre  jour le datalist
    datalist.innerHTML = Array.from(uniqueHarmonises)
      .map(value => `<option value="${value}">`)
      .join('');

    // Sauvegarder l'tat initial pour pouvoir annuler
    const initialState = JSON.parse(JSON.stringify(rows));
    
    // Filtrer les doublons d'affichage
    const seenHarmo = new Set();
    let rowsAffichage = [];
    for (const row of rows) {
      const harmonise = row.apres || (row.suggestions && row.suggestions[0] && row.suggestions[0].harmonise) || '';
      const normHarmo = preprocess(harmonise);
      if (harmonise) {
        if (seenHarmo.has(normHarmo)) continue; // dj affich
        seenHarmo.add(normHarmo);
        rowsAffichage.push(row);
      } else {
        // Pas de valeur harmonise: on affiche toujours la ligne
        rowsAffichage.push(row);
      }
    }
    
    // Gnrer le tableau HTML
    rowsAffichage.forEach((row, i) => {
      const tr = document.createElement('tr');
      // Utilise l'index rel dans rows pour les handlers
      tr.setAttribute('data-idx', rows.indexOf(row));
      tr.style.background = row.source === 'BDD exacte' ? '#f6ffed' :
                           row.source === 'Suggestion unique' ? '#e6fffb' :
                           row.source === 'Suggestion' ? '#e6fffb' :
                           row.source === 'Manuel' ? '#fff7e6' : '#fff1f0';

      const avant = row.avant || '';
      const apres = row.apres || '';
      const suggestions = row.suggestions || [];

      const seenSuggestions = new Set();
      const uniqueSuggestions = [];
      for (const s of suggestions) {
        if (!s.harmonise) continue;
        const norm = preprocess(s.harmonise);
        if (seenSuggestions.has(norm)) continue;
        seenSuggestions.add(norm);
        uniqueSuggestions.push(s);
      }

      tr.innerHTML = `
        <td class="avant-cell">${avant}</td>
        <td class="apres-cell">${apres}</td>
        <td class="suggestions-cell">
          ${uniqueSuggestions.map((s, j) => `
            <button class="suggestion-btn ${apres === s.harmonise ? 'selected' : ''}"
                    data-idx="${rows.indexOf(row)}"
                    data-val="${s.harmonise}"
                    style="background: ${apres === s.harmonise ? '#52c41a' : '#f0f5ff'};
                           color: ${apres === s.harmonise ? '#fff' : '#333'};
                           font-weight: ${apres === s.harmonise ? '600' : 'normal'}">
              ${s.harmonise} (${s.score}%)
            </button>
          `).join(' ')}
        </td>
        <td class="manuel-cell">
          <div style="position: relative;">
            <input type="text" 
                   class="manuel-input" 
                   value="${apres}"
                   list="harmonisation-values"
                   placeholder="Saisie manuelle..."
                   style="width: 100%; padding: 4px; border: 1px solid #d9d9d9; border-radius: 4px;">
          </div>
        </td>
      `;
      tbody.appendChild(tr);
    });
    
    // Afficher la modale
    modal.style.display = 'block';
    
    // Handler pour fermer la modale
    const closeBtn = document.getElementById('close-modal');
    if (closeBtn) {
      closeBtn.onclick = function() {
        modal.style.display = 'none';
      };
    }
    
    // Handler pour le bouton Annuler
    const cancelBtn = document.getElementById('cancel-harmonisation-btn');
    if (cancelBtn) {
      cancelBtn.onclick = function() {
        // Restaurer l'tat initial
        Object.keys(initialState).forEach(idx => {
          rows[idx] = JSON.parse(JSON.stringify(initialState[idx]));
        });

        // Mettre  jour l'affichage du tableau
        tbody.querySelectorAll('tr').forEach(tr => {
          const idx = parseInt(tr.getAttribute('data-idx'));
          const row = rows[idx];
          if (!row) return;

          // Mettre  jour la cellule "aprs"
          const apresCell = tr.querySelector('.apres-cell');
          if (apresCell) {
            apresCell.textContent = row.apres || '';
          }

          // Mettre  jour l'input manuel
          const manuelInput = tr.querySelector('.manuel-input');
          if (manuelInput) {
            manuelInput.value = row.apres || '';
          }

          // Mettre  jour le style de la ligne
          tr.style.background = row.source === 'BDD exacte' ? '#f6ffed' :
                              row.source === 'Suggestion unique' ? '#e6fffb' :
                              row.source === 'Suggestion' ? '#e6fffb' :
                              row.source === 'Manuel' ? '#fff7e6' : '#fff1f0';

          // Mettre  jour les boutons de suggestion
          tr.querySelectorAll('.suggestion-btn').forEach(btn => {
            const suggestionVal = btn.getAttribute('data-val');
            const isSelected = row.apres === suggestionVal;
            btn.style.background = isSelected ? '#52c41a' : '#f0f5ff';
            btn.style.color = isSelected ? '#fff' : '#333';
            btn.style.fontWeight = isSelected ? '600' : 'normal';
          });
        });

        // Fermer la modale
        modal.style.display = 'none';
      };
    }
    
    // Handler pour le bouton Tout valider
    const applyBtn = document.getElementById('apply-harmonisation-btn');
    if (applyBtn) {
      applyBtn.onclick = async function() {
        // Appliquer les harmonisations
        const harmonisedResults = rows.map(row => [row.apres || '']);
        
        // Collecter les nouvelles harmonisations
        const theme = document.getElementById('theme-select').value;
        const newHarmonisations = rows
          .filter(row => row.avant && row.apres && row.source !== 'BDD exacte')
          .map(row => [row.avant, row.apres]);
        
        // Mettre  jour la BDD avec les nouvelles harmonisations
        if (newHarmonisations.length > 0) {
          if (!window.newHarmoRowsByTheme) window.newHarmoRowsByTheme = {};
          if (!window.newHarmoRowsByTheme[theme]) window.newHarmoRowsByTheme[theme] = [];
          window.newHarmoRowsByTheme[theme].push(...newHarmonisations);
          
          // Sauvegarder immdiatement la BDD
          await saveBddWithBackup(window.newHarmoRowsByTheme);
        }
        
        await Excel.run(async (context) => {
          const range = context.workbook.worksheets.getActiveWorksheet().getRange(excelRange);
          range.load(["rowCount", "columnCount"]);
          await context.sync();
          
          // Dterminer la destination (remplacement ou nouvelle colonne)
          const destChoice = document.querySelector('input[name="harmo-dest"]:checked').value;
          let destRange;
          
          if (destChoice === 'replace') {
            destRange = range;
          } else {
            // Insrer  droite
            destRange = range.getOffsetRange(0, 1);
          }
          
          // Charger les proprits ncessaires du destRange
          destRange.load(["rowCount", "columnCount", "address"]);
          await context.sync();
          
          // Ajuster la taille du tableau de rsultats
          while (harmonisedResults.length < range.rowCount) {
            harmonisedResults.push(['']);
          }
          if (harmonisedResults.length > range.rowCount) {
            harmonisedResults.length = range.rowCount;
          }
          
          // Appliquer les valeurs
          destRange.values = harmonisedResults;
          await context.sync();
          
          // Sauvegarder l'tat pour undo
          window.lastHarmonisation = {
            address: destRange.address,
            values: harmonisedResults
          };
          
          // Afficher le bouton undo
          document.getElementById('undo-harmonisation-btn').style.display = 'inline-block';
        });
        
        // Fermer la modale
        modal.style.display = 'none';
        
        // Message de confirmation
        showGlobalMsg('Harmonisation applique avec succs.', 'success');
      };
    }
    
    // Aprs gnration du tableau, attacher les handlers sur les boutons de suggestion
    setTimeout(() => {
      tbody.querySelectorAll('.suggestion-btn').forEach(btn => {
        btn.onclick = function(e) {
          const idx = parseInt(btn.getAttribute('data-idx'));
          const val = btn.getAttribute('data-val');
          
          // Vrifier que l'index est valide et que la ligne existe
          if (isNaN(idx) || !rows[idx]) {
            console.warn('Index invalide ou ligne non trouve:', idx);
            return;
          }
          
          // Mettre  jour la ligne
          rows[idx].apres = val;
          rows[idx].source = 'Suggestion';
          
          // Mettre  jour l'affichage
          const tr = tbody.querySelector(`tr[data-idx='${idx}']`);
          if (tr) {
            const apresCell = tr.querySelector('.apres-cell');
            const manuelInput = tr.querySelector('.manuel-input');
            
            if (apresCell) apresCell.textContent = val;
            if (manuelInput) manuelInput.value = val;
            
            tr.style.background = '#e6fffb';
            
            // Mettre  jour le style des boutons de la ligne
            tr.querySelectorAll('.suggestion-btn').forEach(b => {
              b.style.background = '#f0f5ff';
              b.style.color = '#333';
              b.style.fontWeight = 'normal';
            });
            
            btn.style.background = '#52c41a';
            btn.style.color = '#fff';
            btn.style.fontWeight = '600';
          }
        };
      });
      
      // Handler pour la saisie manuelle
      tbody.querySelectorAll('.manuel-input').forEach(input => {
        input.onchange = function(e) {
          const tr = input.closest('tr');
          if (!tr) return;
          
          const idx = parseInt(tr.getAttribute('data-idx'));
          if (isNaN(idx) || !rows[idx]) {
            console.warn('Index invalide ou ligne non trouve:', idx);
            return;
          }
          
          const val = input.value.trim();
          rows[idx].apres = val;
          rows[idx].source = 'Manuel';
          
          const apresCell = tr.querySelector('.apres-cell');
          if (apresCell) {
            apresCell.textContent = val;
          }
          tr.style.background = '#fff7e6';
          
          // Rinitialiser le style des boutons de suggestion
          tr.querySelectorAll('.suggestion-btn').forEach(b => {
            b.style.background = '#f0f5ff';
            b.style.color = '#333';
            b.style.fontWeight = 'normal';
          });
        };
      });
    }, 0);
    
    // Mettre  jour le bouton de sauvegarde
    updateSaveBddBtnVisibility();
  }

  // Prtraitement avanc
  function preprocess(str) {
    if (!str || typeof str !== 'string') return '';
    let s = str.normalize('NFD').replace(/[�-]/g, c => c.normalize('NFD').replace(/\p{Diacritic}/gu, ''));
    s = s.toUpperCase();
    s = s.replace(/[^A-Z0-9 ]/g, ' '); // caractres spciaux
    s = s.replace(/\b(LE|LA|LES|DE|DES|DU|THE|AND|A|AN|UN|UNE|ET|EN|AU|AUX|SUR|SOUS|DANS|PAR|POUR|AVEC|SANS|CHEZ|VIA|VERS|DEPUIS|JUSQU'A|JUSQUA|JUSQU'|JUSQU'|L'|L'|D'|D'|L'|L')\b/gi, ' '); // mots vides
    s = s.replace(/\bPREMIER\b/g, '1');
    s = s.replace(/\bDEUXIEME\b/g, '2');
    s = s.replace(/\bTROISIEME\b/g, '3');
    s = s.replace(/\bQUATRIEME\b/g, '4');
    s = s.replace(/\bCINQUIEME\b/g, '5');
    s = s.replace(/\s+/g, ' ').trim();
    return s;
  }

  // Fonction pour surligner les diffrences entre deux chanes (simple diff caractre  caractre)
  function highlightDiff(a, b) {
    let res = '';
    for (let i = 0; i < Math.max(a.length, b.length); i++) {
      if (a[i] !== b[i]) {
        res += `<span style='background: #ffe0e0; color: #c00;'>${b[i] || ''}</span>`;
      } else {
        res += b[i] || '';
      }
    }
    return res;
  }

  // Soundex phontique
  function soundex(str) {
    if (!str) return '';
    str = str.toUpperCase().replace(/[^A-Z]/g, '');
    if (!str) return '';
    const first = str[0];
    const map = {A:'',E:'',I:'',O:'',U:'',Y:'',H:'',W:'',B:'1',F:'1',P:'1',V:'1',C:'2',G:'2',J:'2',K:'2',Q:'2',S:'2',X:'2',Z:'2',D:'3',T:'3',L:'4',M:'5',N:'5',R:'6'};
    let out = first;
    let prev = map[first] || '';
    for (let i = 1; i < str.length; i++) {
      const code = map[str[i]] || '';
      if (code !== prev) out += code;
      if (code) prev = code;
    }
    out = out.replace(/0/g, '');
    return (out + '000').slice(0, 4);
  }
  // Surlignage des diffrences
  function highlightDiffInline(a, b) {
    let res = '';
    for (let i = 0; i < Math.max(a.length, b.length); i++) {
      if ((a[i]||'') !== (b[i]||'')) res += `<span style='background:#fff59d;color:#c00;border-radius:3px;padding:0 2px;'>${b[i]||''}</span>`;
      else res += b[i]||'';
    }
    return res;
  }

  // Ajout handler bouton sauvegarde BDD
  function getTodayIsoDate() {
    const d = new Date();
    return d.toISOString().slice(0,10);
  }

  // Sauvegarde silencieuse pour l'harmonisation
  async function saveBddWithBackup(newRowsByTheme = {}) {
    if (!window.bddWorkbook) {
      showGlobalMsg('Aucune BDD charge.', 'error');
      return;
    }
    const newWb = XLSX.utils.book_new();
    window.bddWorkbook.SheetNames.forEach(sheetName => {
      const oldSheet = window.bddWorkbook.Sheets[sheetName];
      const oldData = XLSX.utils.sheet_to_json(oldSheet, { header: 1 });
      let allRows = [...oldData];
      // Ajout des nouvelles harmonisations pour ce thme
      const newRows = newRowsByTheme[sheetName] || [];
      newRows.forEach(row => {
        if (!allRows.some(r => r[0] === row[0] && r[1] === row[1])) {
          allRows.push(row);
        }
      });
      const ws = XLSX.utils.aoa_to_sheet(allRows);
      XLSX.utils.book_append_sheet(newWb, ws, sheetName);
    });
    const wbout = XLSX.write(newWb, { bookType: 'xlsx', type: 'array' });
    
    try {
      // Sauvegarder les donnes dans les paramtres Office
      Office.context.document.settings.set('bddWorkbookData', wbout);
      Office.context.document.settings.saveAsync();
      window.bddWorkbook = newWb;
      showGlobalMsg('BDD mise  jour avec succs.', 'success');
    } catch (err) {
      console.error('Erreur lors de la sauvegarde:', err);
      showGlobalMsg('Erreur lors de la mise  jour de la BDD.', 'error');
    }
  }

  // Sauvegarde avec dialogue de fichier
  async function saveBddToFile() {
    if (!window.bddWorkbook) {
      showGlobalMsg('Aucune BDD charge.', 'error');
      return;
    }
    const wbout = XLSX.write(window.bddWorkbook, { bookType: 'xlsx', type: 'array' });
    const blob = new Blob([wbout], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    
    try {
      // Configurer les options pour le dialogue de sauvegarde
      const opts = {
        suggestedName: `bdd_harmonisee_${getTodayIsoDate()}.xlsx`,
        types: [{
          description: 'Fichier Excel',
          accept: {
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': ['.xlsx']
          }
        }]
      };

      // Ouvrir le dialogue de sauvegarde
      const handle = await window.showSaveFilePicker(opts);
      const writable = await handle.createWritable();
      await writable.write(blob);
      await writable.close();
      
      showGlobalMsg('BDD sauvegarde avec succs.', 'success');
    } catch (err) {
      if (err.name !== 'AbortError') {
        console.error('Erreur lors de la sauvegarde:', err);
        showGlobalMsg('Erreur lors de la sauvegarde de la BDD.', 'error');
      }
    }
  }

  document.addEventListener('DOMContentLoaded', function() {
    const saveBtn = document.getElementById('save-bdd-btn');
    if (saveBtn) {
      saveBtn.style.display = 'inline-block';
      saveBtn.onclick = saveBddToFile;
    }
    updateSaveBddBtnVisibility();
  });

